import React, { useState, useEffect } from 'react';
import { Layout, Typography, Table, notification, Row, Col, Button, Modal } from 'antd';
import './index.css'
import network from '../providers/network';

const tableColumns = [
  {
    title: "Name",
    dataIndex: "name",
    key: 'id'
  }
]


const { Content, Footer } = Layout
const HomePage = () => {
  const [alert, alertContext] = notification.useNotification()
  const [apps, setApps] = useState([])
  const [isAddAppModalOpen, setAddAppModalOpen] = useState(false)
  const [loadingAddApp, setLoadingAddApp] = useState(false)

  const onTableRowAction = ((row, index) => ({
    onClick: () => console.log(row.id)
  }))

  const onAddAppSubmit = () => {
    setLoadingAddApp(true)
    setTimeout(() => {
      setLoadingAddApp(false)
      setAddAppModalOpen(false)
      alert.success({description:"New app added successfully"})
    }, 3000)
  }

  useEffect(() => {
    network.get("_admin/apps").then(res => {
      setApps(res.data)
    }).catch(err => {
      alert.error({
        title: "Error",
        description: `Error while getting apps data, please contact the administrator. Message: ${err.message}`
      })
    })
  }, [alert])

  return (
    <>
      {alertContext}
      <Layout className='main'>
        <Layout>
          <Content className='content'>
            <Row justify="space-between">
              <Col><Typography.Title level={3}>Apps List</Typography.Title></Col>
              <Col>
                <Button
                  type='primary'
                  onClick={() => setAddAppModalOpen(true)}>
                  Add App
                </Button>
              </Col>
            </Row>
            <Table
              onRow={onTableRowAction}
              className='apps-table'
              columns={tableColumns}
              dataSource={apps}
              rowKey="id" />
          </Content>
          <Footer className='footer'>
            Dynapgen @ 2023. Made with ❤️
          </Footer>
        </Layout>
      </Layout>
      <Modal
        open={isAddAppModalOpen}
        onOk={onAddAppSubmit}
        onCancel={() => setAddAppModalOpen(false)}
        confirmLoading={loadingAddApp}
        closable={false}>
        Halo Gan Hehe
      </Modal>
    </>
  )
}

export default HomePage
export const Head = () => {
  return (
    <title>Home Page</title>
  )
}